.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color:#1E3050;
  font-size: 0.75rem;
  font-weight: 800;
  letter-spacing: 0.1em;
  background-color: #fff;

  &__section {
    display: flex;
    align-items: center;
    width: calc(50% - 2rem);
    gap: 1.875rem;
  }

  &__section > * {
    margin: 0.75rem 0;
  }

  &__left-section {
    justify-content: flex-end;
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 0 0.625rem;
  }

  &__user-name_container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.875rem;
    height: 1.875rem;
    border-radius: 100px;
    background-color: #FAD338;
  }

  &__drop-down-menu {
    display: none;
    cursor: pointer;
  }
}


@media screen and (max-width: 1000px) {
  .header {
    &__drop-down-menu {
      display: block;
    }

    &__link {
      display: none;
    }
  }
}